<template>
    <view class="release">
        <view class="content">
            <view class="u-demo-block__content">
                <!-- 注意，如果需要兼容微信小程序，最好通过setRules方法设置rules规则 -->
                <u--form labelPosition="left" :model="model1" ref="form1">
                    <u-form-item label="姓名" prop="userInfo.name" borderBottom ref="item1">
                        <u--input v-model="model1.userInfo.name" border="none" placeholder="姓名,只能为中文"></u--input>
                    </u-form-item>
                    <u-form-item label="性别" prop="userInfo.sex" borderBottom @click="showSex = true; hideKeyboard()"
                        ref="item1">
                        <u--input v-model="model1.userInfo.sex" disabled disabledColor="#ffffff" placeholder="请选择性别"
                            border="none"></u--input>
                        <u-icon slot="right" name="arrow-right"></u-icon>
                    </u-form-item>
                    <u-form-item label="电话" prop="userInfo.phone" borderBottom ref="item1">
                        <u--input v-model="model1.userInfo.phone" border="none" placeholder="请输入电话"></u--input>
                    </u-form-item>
                    <u-form-item label="生日" prop="userInfo.birthday" borderBottom
                        @click="showBirthday = true; hideKeyboard()" ref="item1">
                        <u--input v-model="model1.userInfo.birthday" disabled disabledColor="#ffffff" placeholder="请选择生日"
                            border="none"></u--input>
                        <u-icon slot="right" name="arrow-right"></u-icon>
                    </u-form-item>
                    <u-form-item label="身高" prop="userInfo.height" borderBottom>
                        <u--input v-model="model1.userInfo.height" border="none" placeholder="请输入身高(cm)"></u--input>
                    </u-form-item>
                    <u-form-item label="学历" prop="userInfo.education" borderBottom
                        @click="showEducation = true; hideKeyboard()" ref="item1">
                        <u--input v-model="model1.userInfo.education" disabled disabledColor="#ffffff" placeholder="请选择学历"
                            border="none"></u--input>
                        <u-icon slot="right" name="arrow-right"></u-icon>
                    </u-form-item>
                    <u-form-item label="单位" prop="userInfo.unit" borderBottom ref="item1">
                        <u--input v-model="model1.userInfo.unit" border="none" placeholder="请输入单位"></u--input>
                    </u-form-item>
                    <u-form-item label="收入" prop="userInfo.income" borderBottom @click="showIncome = true; hideKeyboard()"
                        ref="income">
                        <u--input v-model="model1.userInfo.income" disabled disabledColor="#ffffff" placeholder="请选择收入范围"
                            border="none"></u--input>
                        <u-icon slot="right" name="arrow-right"></u-icon>
                    </u-form-item>
                    <u-form-item label="婚否" prop="radiovalue1" borderBottom ref="item2">
                        <u-radio-group v-model="model1.userInfo.marriage">
                            <u-radio :customStyle="{ marginRight: '16px' }" v-for="(item, index) in marriageList" :key="index"
                                :label="item.name" :name="item.name">
                            </u-radio>
                        </u-radio-group>
                    </u-form-item>
                    <u-form-item label="简介" prop="intro" borderBottom>
                        <u--textarea placeholder="写写爱好特长啊等等" v-model="model1.userInfo.intro" count></u--textarea>
                    </u-form-item>
                    <u-form-item label="照片" prop="intro" borderBottom>
                        <view class="u-demo-block">
                            <view class="u-demo-block__content">
                                <view class="u-page__upload-item">
                                    <u-upload :fileList="fileList" @afterRead="afterRead" @delete="deletePic" name="3"
                                        multiple :maxCount="10" :previewFullImage="true"></u-upload>
                                </view>
                            </view>
                        </view>
                    </u-form-item>
                </u--form>
                <u-button type="primary" text="提交" customStyle="margin-top: 50px" @click="submit"></u-button>
                <u-button type="error" text="重置" customStyle="margin-top: 10px" @click="reset"></u-button>
                <u-action-sheet :show="showSex" :actions="actions" title="请选择性别" description="" @close="showSex = false"
                    @select="sexSelect" />
                <u-action-sheet :show="showIncome" :actions="incomeActions" title="请选择收入范围" description=""
                    @close="showIncome = false" @select="selectIncome" />
                <u-action-sheet :show="showEducation" :actions="educationList" title="请选择学历" description=""
                    @close="showEducation = false" @select="selectEducation" />
                <u-datetime-picker :min-date="minDate" :max-date="maxDate" :show="showBirthday" :value="birthday" mode="date" closeOnClickOverlay
                    @confirm="birthdayConfirm" @cancel="birthdayClose" @close="birthdayClose" />
            </view>
        </view>
    </view>
</template>

<script>
export default {
    data() {
        return {

            model1: {
                userInfo: {
                    name: '',
                    sex: '',  // 性别
                    birthday: '', // 爱好
                    income: '', // 收入范围
                    height: '', // 身高
                    education: '', // 学历
                    unit: '', // 单位
                    phone: '', // 电话
                    marriage: '', // 婚否
                    intro: '', // 简介
                }
            },
            birthday: Number(new Date()), // 弹窗显示值
            minDate: Number(new Date('1950-01-01')), // 弹窗最小值
            maxDate: Number(new Date()), // 弹窗最大值
            showSex: false, // 性别弹框
            actions: [{ name: '男', }, { name: '女', }],
            showIncome: false, // 收入范围弹框
            showEducation: false, // 学历弹窗显示
            showBirthday: false, // 显示生日
            incomeActions: [{ name: '8000以下', }, { name: '8000-12000', }, { name: '12000-16000', }, { name: '16000-20000', }, { name: '20000-25000', }, { name: '25000以上', }],
            marriageList: [{ name: '未婚', disabled: false }, { name: '离异', disabled: false }, { name: '丧偶', disabled: false }],
            educationList: [
                { name: '小学', disabled: false },
                { name: '初中', disabled: false },
                { name: '高中', disabled: false },
                { name: '职高', disabled: false },
                { name: '中专', disabled: false },
                { name: '技校', disabled: false },
                { name: '专科', disabled: false },
                { name: '本科', disabled: false },
                { name: '硕士', disabled: false },
                { name: '博士', disabled: false },
            ],
            fileList: [], // 照片
            rules: { // 校验
                'userInfo.name': [{
                    type: 'string',
                    required: true,
                    message: '请填写姓名',
                    trigger: ['blur', 'change']
                }, {
                    // 此为同步验证，可以直接返回true或者false，如果是异步验证，稍微不同，见下方说明
                    validator: (rule, value, callback) => {
                        // 调用uView自带的js验证规则，详见：https://www.uviewui.com/js/test.html
                        return uni.$u.test.chinese(value);
                    },
                    message: "姓名必须为中文",
                    // 触发器可以同时用blur和change，二者之间用英文逗号隔开
                    trigger: ["change", "blur"],
                }],
                code: {
                    type: 'string',
                    required: true,
                    len: 4,
                    message: '请填写4位验证码',
                    trigger: ['blur']
                },
                'userInfo.sex': {
                    type: 'string',
                    max: 1,
                    required: true,
                    message: '请选择男或女',
                    trigger: ['blur', 'change']
                }
            },
        }
    },
    onReady() {
        // 如果需要兼容微信小程序，并且校验规则中含有方法等，只能通过setRules方法设置规则
        this.$refs.form1.setRules(this.rules)
    },
    methods: {
        navigateBack() {
            uni.navigateBack()
        },
        // 性别选择
        sexSelect(e) {
            this.model1.userInfo.sex = e.name
            this.$refs.form1.validateField('userInfo.sex')
        },
        // 收入范围选择
        selectIncome(e) {
            this.model1.userInfo.income = e.name
            this.$refs.form1.validateField('userInfo.income')
        },
        selectEducation(e) {
            this.model1.userInfo.education = e.name
            this.$refs.form1.validateField('userInfo.education')
        },
        birthdayClose() {
            this.showBirthday = false
            this.$refs.form1.validateField('userInfo.birthday')
        },
        birthdayConfirm(e) {
            this.showBirthday = false
            this.model1.userInfo.birthday = uni.$u.timeFormat(e.value, 'yyyy-mm-dd')
            this.$refs.form1.validateField('userInfo.birthday')
        },
        submit() {
            // 如果有错误，会在catch中返回报错信息数组，校验通过则在then中返回true
            this.$refs.form1.validate().then(res => {
                console.log('rrr', this.model1.userInfo);
                uni.$u.toast('校验通过')
            }).catch(errors => {
                uni.$u.toast('校验失败')
            })
        },
        reset() {
            // const validateList = ['userInfo.name', 'userInfo.sex', 'radiovalue1', 'checkboxValue1', 'intro', 'hotel', 'code']
            // this.$refs.form1.resetFields()
            // this.$refs.form1.clearValidate()
            // setTimeout(() => {
            //     this.$refs.form1.clearValidate(validateList)
            //     // 或者使用 this.$refs.form1.clearValidate()
            // }, 10)
        },
        hideKeyboard() {
            uni.hideKeyboard()
        },
        // 删除图片
        deletePic(event) {
            this[`fileList${event.name}`].splice(event.index, 1)
        },
        // 新增图片
        async afterRead(event) {
            // 当设置 mutiple 为 true 时, file 为数组格式，否则为对象格式
            let lists = [].concat(event.file)
            let fileListLen = this[`fileList${event.name}`].length
            lists.map((item) => {
                this[`fileList${event.name}`].push({
                    ...item,
                    status: 'uploading',
                    message: '上传中'
                })
            })
            for (let i = 0; i < lists.length; i++) {
                const result = await this.uploadFilePromise(lists[i].url)
                let item = this[`fileList${event.name}`][fileListLen]
                this[`fileList${event.name}`].splice(fileListLen, 1, Object.assign(item, {
                    status: 'success',
                    message: '',
                    url: result
                }))
                fileListLen++
            }
        },
        // 上传照片接口
        uploadFilePromise(url) {
            return new Promise((resolve, reject) => {
                let a = uni.uploadFile({
                    url: 'http://www.example.com/upload', // 仅为示例，非真实的接口地址
                    filePath: url,
                    name: 'file',
                    formData: {
                        user: 'test'
                    },
                    success: (res) => {
                        setTimeout(() => {
                            resolve(res.data.data)
                        }, 1000)
                    }
                });
            })
        },
    },
}
</script>

<style lang="scss">
.release {
    width: 100%;
    height: auto;
    background: #f2f3f5;
    padding: 20rpx 20rpx 0 20rpx;
    font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun;
    font-size: 14px;

    .content {
        background: #FFF;
    }
}</style>
